<template>
  <div class="confirm uk-text-center">
    <h1 v-if="isConfirmed.JDQYSDS" class="uk-margin-large-top uk-text-danger">季度所得税申报表申报结果</h1>
    <h1 v-if="!isConfirmed.JDQYSDS" class="uk-margin-large-top uk-text-danger">数据审核无误，请点击“确认申报”进行申报！</h1>
    <div v-if="isConfirmed.JDQYSDS" style="width: 900px; margin: 50px auto; font-size: 16px; line-height: 25px"
         class="uk-text-danger uk-text-left">
      申报数据已上报，请确认已签订三方协议的缴税账户余额是否充足，否则无法网上申报自动扣款。
      请您关注申报状态及税款扣转的银行账号余额变动情况。
      如自动扣款未成功，请于本月申报期最后一日下午到国税主管税务机关缴纳税款！
      申报期最后一日中午12时，网上办税平台停止自动扣款处理功能。
    </div>
    <div v-show="!isConfirmed.JDQYSDS" style="margin-top: 20px">
      <button @click="confirm" class="uk-button uk-button-primary"> 确认申报</button>
    </div>
  </div>
</template>
<script>
  import mixins from '../../mixins/common'
  import { mapState } from 'vuex'

  export default {
    mixins: [mixins],
    data() {
      return {}
    },
    computed: {
      ...mapState(['isConfirmed'])
    },
    methods: {
      confirm() {
        this.$store.commit('setConfirm', { key: 'JDQYSDS', value: true })
      }
    }
  }
</script>



